export const putUserProfileInfo = async(id, payload) => {
    const {
        username,
        userAccount,
        userEmail,
        userPassword,
        userIntroduction,
        userAvatar
    } = payload
    
    try {
        const token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6MjQsImFjY291bnQiOiJ1c2VyMSIsImVtYWlsIjoidXNlcjFAZXhhbXBsZS5jb20iLCJuYW1lIjoiVmlyZ2luaWUgTG93ZSIsImF2YXRhciI6Imh0dHBzOi8vaS5wcmF2YXRhci5jYy8zMDA_.Vw5jHTbPSw6OdYPyFV6Rd1YarbSQ_cMLNmit8iPNWmo'
        const headers = {
            Authorization: `Bearer ${token}`
        }

        localStorage.setItem('token', token);
        const response = await axios.put( `${baseUrl}/api/users/${id}`, {
            name: username,
            account: userAccount,
            email: userEmail,
            password: userPassword,
            checkPassword: userPassword,
            introduction: userIntroduction,
            avatar: userAvatar
        }, { headers })
        return response.data

    } catch (error) {
        console.error(error)
    }
}